.novelty{
  margin-top: 78px;
  background-color: var(--mirage-3);

  &__navigation{
    display: flex;
    justify-content: center;
    gap: 30px;
  }

  &__swiper-pagination{
    position: relative;
    color: var(--white);
    font-size: 20px;
    font-style: var(--normal);
    font-weight: var(--fon500);
    line-height: var(--normal);
    max-width: 60px;
    max-height: 25px;
  }

  &__swiper{
    margin-top: 52px;
  }

  &__menu{
    padding-top: 50px;
  }
}


@media (max-width: 1400px){

 .novelty{

    &__swiper-pagination{
      bottom: -17px;
    }

 }
}

@media (max-width: 800px){

  .novelty{

    &__menu{
      flex-direction: column;
      row-gap: 15px;
    }

    &__swiper-pagination{
      bottom: 0;
    }

    &__swiper{
      margin-top: 35px;
    }
 }
}




